import React from "react";
import { Avatar, Grid } from "@mui/material";
import MUIDataTable from "mui-datatables";

function Charachters({
  charachters,
  totalData,
  getCharachters,
  setStatus,
  setSpecies,
}) {
  const columns = [
    {
      name: "id",
      label: "ID",
      options: {
        filter: false,
        sort: false,
      },
    },
    {
      name: "image",
      label: "Avatar",
      options: {
        customBodyRender: (value) => {
          return (
            <Grid container>
              <Avatar
                src={value}
                sx={{ height: "90px", width: "90px" }}
              ></Avatar>
            </Grid>
          );
        },
        filter: false,
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: false,
        sort: false,
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "species",
      label: "Species",
      options: {
        filter: true,
        sort: false,
      },
    },
  ];

  const options = {
    filterType: "dropdown",
    serverSide: true,
    rowsPerPage: 20,
    rowsPerPageOptions: [20],
    count: totalData,
    onTableChange: async (action, tableState) => {
      if (action === "changePage") {
        const nextPage = tableState.page + 1;
        getCharachters(nextPage);
      }
    },
    onFilterChange: (changedColumn, filterList) => {
      if (changedColumn === "species") {
        setSpecies(filterList[4]);
      }
      if (changedColumn === "status") {
        setStatus(filterList[3]);
      }
    },
  };

  return (
    <MUIDataTable
      title={"Rick N Morty"}
      data={charachters}
      columns={columns}
      options={options}
    />
  );
}

export default Charachters;
