import React, { useCallback, useState } from "react";
import Header from "../../components/Header/Header";
import Charachters from "../../components/Charachters/Charachters";
import { Grid } from "@mui/material";

function Home() {
  const [charachters, setCharachters] = React.useState([].slice(0, 50));
  const [totalData, setTotalData] = useState(0);
  const [status, setStatus] = useState("");
  const [species, setSpecies] = useState("");

  const getCharachters = useCallback(
    async (nextPage) => {
      const fetchApiResponse = await fetch(
        `https://rickandmortyapi.com/api/character/?page=${nextPage}&status=${status}&species=${species}`
      );
      const apiResponseData = await fetchApiResponse.json();
      setCharachters(apiResponseData.results);
      setTotalData(apiResponseData.info.count);
    },
    [status, species]
  );

  React.useEffect(() => {
    getCharachters();
  }, [species, status, getCharachters]);

  return (
    <div>
      <Header />
      <Grid container justifyContent="center">
        <Grid item xs={10}>
          <Charachters
            charachters={charachters}
            setCharachters={setCharachters}
            totalData={totalData}
            setTotalData={setTotalData}
            getCharachters={getCharachters}
            setStatus={setStatus}
            setSpecies={setSpecies}
          />
        </Grid>
      </Grid>
    </div>
  );
}

export default Home;
